/**
 * LocalCache contains APIs for local operations.
 * @module LocalCache
 */

import { getSessionId } from './core';
import {
	ConversationItem,
	UpdateConversationParams,
	SessionParams,
	RemoveLocalConversationParams,
} from '../types/localCacheApi';
import { AsyncResult } from '../types/common';
import ErrorEvent from '../error/error';
import { logger } from '../logger';
import { checkStatus } from '../apis/utils';
import { Code } from '../status';
import { Connection } from '../engineCore/connection';

const ConversationTypes = ['singleChat', 'groupChat'];

/**
 * Gets the local conversation list.
 *
 * ```typescript
 * connection.localCache.getLocalConversations()
 * ```
 */

/**
 * 获取本地会话列表。
 *
 * ```typescript
 * connection.localCache.getLocalConversations()
 * ```
 */
async function getLocalConversations(
	this: Connection
): Promise<AsyncResult<ConversationItem[]>> {
	logger.debug('Call getLocalConversations');
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	try {
		const list =
			(await this._localCache?.getInstance()?.getConversationList()) ||
			[];
		const dt = await Promise.all(
			list.map(
				async ({
					conversationId,
					lastMessageId,
					unReadCount,
					conversationType,
					customField,
				}) => {
					return {
						conversationId: conversationId,
						conversationType,
						unReadCount: unReadCount || 0,
						customField,
						lastMessage: lastMessageId
							? await this._localCache
									?.getInstance()
									?.getMessageByServerMsgId(lastMessageId)
							: undefined,
					};
				}
			)
		);
		return {
			type: 0,
			data: dt,
		};
	} catch (error) {
		const e = ErrorEvent.create({
			type: Code.LOCAL_DB_OPERATION_FAILED,
			message: 'getLocalConversations is failed',
			data: error,
		});
		throw e;
	}
}

/**
 * Sets the local conversation custom field.
 *
 * ```typescript
 * connection.localCache.setLocalConversationCustomField({conversationId: 'conversationId', conversationType: 'singleChat', customField: {key:'value'} })
 * ```
 */

/**
 * 设置本地会话自定义字段。
 *
 * ```typescript
 * connection.localCache.setLocalConversationCustomField({conversationId: 'conversationId', conversationType: 'singleChat', customField: {key:'value'} })
 * ```
 */

async function setLocalConversationCustomField(
	this: Connection,
	params: UpdateConversationParams
): Promise<void> {
	logger.debug('Call setLocalConversationCustomField', params);
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { conversationType, conversationId, customField } = params;
	if (!ConversationTypes.includes(conversationType)) {
		throw Error(
			'"Invalid parameter conversationType": ' + conversationType
		);
	}
	if (typeof conversationId !== 'string' || conversationId === '') {
		throw Error('Invalid parameter: "conversationId"');
	}
	if (typeof customField !== 'object') {
		throw Error('Invalid parameter: "customField"');
	}
	try {
		await this._localCache?.getInstance()?.updateLocalConversation(
			getSessionId({
				conversationId,
				conversationType,
			}),
			{
				customField,
			}
		);
	} catch (error) {
		const e = ErrorEvent.create({
			type: Code.LOCAL_DB_OPERATION_FAILED,
			message: 'setLocalConversationCustomField is failed',
			data: error,
		});
		throw e;
	}
}

/**
 * Gets a local conversation.
 *
 * ```typescript
 * connection.localCache.getLocalConversation({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */

/**
 * 获取单个本地会话。
 *
 * ```typescript
 * connection.localCache.getLocalConversation({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */
async function getLocalConversation(
	this: Connection,
	params: SessionParams
): Promise<AsyncResult<ConversationItem | undefined>> {
	logger.debug('Call getLocalConversation', params);
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { conversationType, conversationId } = params;
	if (!ConversationTypes.includes(conversationType)) {
		throw Error(
			'"Invalid parameter conversationType": ' + conversationType
		);
	}

	if (typeof conversationId !== 'string' || conversationId === '') {
		throw Error('Invalid parameter: "conversationId"');
	}

	try {
		const dt = await this._localCache
			?.getInstance()
			?.getConversationBySessionId(
				getSessionId({ conversationId, conversationType })
			);

		return {
			type: 0,
			data: dt
				? {
						conversationId: dt.conversationId,
						conversationType,
						unReadCount: dt.unReadCount || 0,
						customField: dt.customField,
						lastMessage: dt.lastMessageId
							? await this._localCache
									?.getInstance()
									?.getMessageByServerMsgId(dt.lastMessageId)
							: undefined,
				  }
				: undefined,
		};
	} catch (error) {
		const e = ErrorEvent.create({
			type: Code.LOCAL_DB_OPERATION_FAILED,
			message: 'getLocalConversation is failed',
			data: error,
		});
		throw e;
	}
}

/**
 * Removes a local conversation.
 *
 * ```typescript
 * connection.localCache.removeLocalConversation({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */

/**
 * 删除单个本地会话。
 *
 * ```typescript
 * connection.localCache.removeLocalConversation({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */
async function removeLocalConversation(
	this: Connection,
	params: RemoveLocalConversationParams
): Promise<void> {
	logger.debug('Call removeLocalConversation', params);
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const {
		conversationType,
		conversationId,
		isRemoveLocalMessage = true,
	} = params;
	if (!ConversationTypes.includes(conversationType)) {
		throw Error(
			'"Invalid parameter conversationType": ' + conversationType
		);
	}

	if (typeof conversationId !== 'string' || conversationId === '') {
		throw Error('Invalid parameter: "conversationId"');
	}
	const sessionId = getSessionId({ conversationId, conversationType });
	try {
		await this._localCache
			?.getInstance()
			?.removeConversationBySessionId(sessionId);

		isRemoveLocalMessage &&
			(await this._localCache
				?.getInstance()
				?.removeMsgBySessionId(sessionId));
	} catch (error) {
		const e = ErrorEvent.create({
			type: Code.LOCAL_DB_OPERATION_FAILED,
			message: 'removeLocalConversation is failed',
			data: error,
		});
		throw e;
	}
}

/**
 * Resets the number of unread messages in a conversation to 0.
 *
 * ```typescript
 * connection.localCache.clearConversationUnreadCount({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */

/**
 * 对本地会话的未读消息数清零。
 *
 * ```typescript
 * connection.localCache.clearConversationUnreadCount({conversationId: 'conversationId', conversationType: 'singleChat' })
 * ```
 */
async function clearConversationUnreadCount(
	this: Connection,
	params: SessionParams
): Promise<void> {
	logger.debug('Call clearConversationUnreadCount', params);
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { conversationType, conversationId } = params;
	if (!ConversationTypes.includes(conversationType)) {
		throw Error(
			'"Invalid parameter conversationType": ' + conversationType
		);
	}
	if (typeof conversationId !== 'string' || conversationId === '') {
		throw Error('Invalid parameter: "conversationId"');
	}
	try {
		await this._localCache?.getInstance()?.updateLocalConversation(
			getSessionId({
				conversationId,
				conversationType,
			}),
			{
				unReadCount: 0,
				unreadCountClearTimestamp: new Date().getTime(),
			}
		);
	} catch (error) {
		const e = ErrorEvent.create({
			type: Code.LOCAL_DB_OPERATION_FAILED,
			message: 'clearConversationUnreadCount is failed',
			data: error,
		});
		throw e;
	}
}

export {
	getLocalConversations,
	setLocalConversationCustomField,
	getLocalConversation,
	removeLocalConversation,
	clearConversationUnreadCount,
};
