import { openDB, DBSchema, IDBPDatabase } from 'idb';
import { logger } from '../logger';
import { getSessionId } from './utils';
import {
	LocalMessageType,
	MessageStatus,
	MessageUpdateOptions,
	LocalConversation,
	ConversationType,
	ConversationUpdateInfo,
	LocalMessageBody,
	CacheConversationMap,
} from './types';

const START_TIME = new Date('2023/01/01');

interface LocalDB extends DBSchema {
	conversationList: {
		value: LocalConversation;
		key: string; // 主键
		indexes: { updateTime: number }; // 索引键
	};
	message: {
		value: LocalMessageType;
		key: string;
		indexes: {
			sessionId: string;
			time: number;
			serverMsgId: string;
			// 会话与会话每条消息的时间
			sessionTime: [string, number];
		};
	};
}

type LocalDBStoreName = 'conversationList' | 'message';

class LocalCache {
	private localCache?: IDBPDatabase<LocalDB>;
	private user: string;
	private onInit: () => void;
	private static instance?: LocalCache;
	conversationMap: CacheConversationMap;
	constructor(params: {
		user: string;
		dbName: string;
		version: number;
		onInit: () => void;
	}) {
		const { user, dbName, version, onInit } = params;
		this.localCache = undefined;
		this.user = '';
		this.conversationMap = {};
		this.onInit = onInit;
		this.init({ user, dbName, version });
	}

	private async init(options: {
		dbName: string;
		version: number;
		user: string;
	}) {
		logger.debug('init local cache', options);
		const { dbName, version, user } = options;
		this.user = user;
		try {
			const db = await openDB<LocalDB>(dbName, version, {
				upgrade(db, oldVersion, newVersion, transaction) {
					// 首次创建数据库
					if (oldVersion < 1) {
						logger.debug('IndexedDB oldVersion', oldVersion);
						const messageStore = db.createObjectStore('message', {
							keyPath: 'id', // 消息id
						});
						messageStore.createIndex('sessionId', 'sessionId');
						messageStore.createIndex('time', 'time');
						messageStore.createIndex('serverMsgId', 'serverMsgId', {
							unique: true,
						});
						messageStore.createIndex(
							'sessionTime',
							['sessionId', 'time'],
							{
								unique: true,
							}
						);

						const conversationStore = db.createObjectStore(
							'conversationList',
							{
								keyPath: 'sessionId', // sessionId
							}
						);
						conversationStore.createIndex(
							'updateTime',
							'updateTime'
						);
					}
					// 数据库升级
					if (oldVersion < 2) {
						logger.debug('IndexedDB version', 2);
						const store = transaction.objectStore('message');
						store.deleteIndex('sessionTime');
						// remove sessionTime unique
						store.createIndex('sessionTime', ['sessionId', 'time']);
					}
				},
			});

			this.localCache = db;
			if (!LocalCache.instance) {
				LocalCache.instance = this;
				this.onInit();
			}
		} catch (error) {
			logger.debug('open db error');
		}
	}

	static getInstance() {
		return LocalCache.instance;
	}

	/**
	 * IDBDatabase 接口的 close() 方法立即返回并在单独的线程中关闭连接。
	 * 直到使用此连接创建的所有事务都完成后，连接才真正关闭。
	 * 一旦调用此方法，就不能为此连接创建新的事务。 如果关闭操作挂起，则创建事务的方法会抛出异常。
	 */

	close() {
		this.localCache?.close();
		this.clearConversationMap();
		LocalCache.instance = undefined;
		logger.debug('close db success');
	}

	/**
	 *
	 * @param conversationId 用户ID或者群组ID
	 * @param conversationType 会话类型
	 * @param info 会话其他信息
	 * @param isRecordUnreadCount 是否记录未读数
	 */
	async storeConversation(
		conversationId: string,
		conversationType: ConversationType,
		info: ConversationUpdateInfo,
		isRecordUnreadCount?: boolean
	) {
		try {
			const sessionId = getSessionId({
				conversationId,
				conversationType,
			});

			let conversation = {
				conversationId,
				conversationType,
				sessionId: sessionId,
				...info,
			};

			const dt =
				this.conversationMap[sessionId] ||
				(await this.getConversationBySessionId(sessionId)) ||
				conversation;

			// 如果本地缓存不存在当前会话
			if (!this.conversationMap[sessionId]) {
				this.conversationMap[sessionId] = dt;
			}

			// 存在本地数据
			if (dt) {
				// 表示需要增加未读数
				if (isRecordUnreadCount) {
					this.conversationMap[sessionId].unReadCount =
						(dt.unReadCount || 0) + 1;
					// 不更新当前sessionId未读数量
					conversation.unReadCount =
						this.conversationMap[sessionId].unReadCount || 0;
				}
				conversation = { ...dt, ...conversation };
			}
			await this.putConversationToDB(conversation);
		} catch (error) {
			logger.debug('store conversation error', error);
		}
	}
	/**
	 * 向本地数据库中插入会话
	 * @param conversation
	 */
	async putConversationToDB(conversation: LocalConversation) {
		try {
			await this.localCache?.put('conversationList', conversation);
		} catch (error) {
			logger.debug('put conversation to local error', error);
		}
	}
	/**
	 * 根据sessionId获取本地会话
	 * @param sessionId
	 * @returns
	 */
	async getConversationBySessionId(sessionId: string) {
		try {
			return await this.localCache?.get('conversationList', sessionId);
		} catch (error) {
			logger.debug('get conversation by sessionId error', error);
		}
	}
	/**
	 * 根据sessionI删除本地会话
	 * @param sessionId
	 * @returns
	 */
	async removeConversationBySessionId(sessionId: string) {
		try {
			if (this.conversationMap[sessionId]) {
				delete this.conversationMap[sessionId];
			}
			return this.localCache?.delete('conversationList', sessionId);
		} catch (error) {
			logger.debug('remove conversation by sessionId error', error);
		}
	}

	/**
	 * 获取本地会话列表
	 * @returns
	 */
	async getConversationList() {
		const dt = [];
		try {
			let cursor = await this.localCache
				?.transaction('conversationList')
				.store.index('updateTime')
				.openCursor(null, 'prev');
			while (cursor) {
				dt.push(cursor.value);
				cursor = await cursor.continue();
			}
			return dt;
		} catch (error) {
			logger.debug('get local conversationList error', error);
		}
	}

	/**
	 * 存储消息
	 * @param message 消息对象
	 * @param status 消息发送状态
	 * @param isHistoryMessage 是否为服务器下发的历史消息
	 */
	async storeMessage(
		message: LocalMessageBody,
		status: MessageStatus,
		isHistoryMessage?: boolean
	) {
		try {
			const dt = { ...message } as LocalMessageType;
			const updateInfo: ConversationUpdateInfo = {};
			let isFromOtherMsg = false;
			let conversationId = dt.to;
			let isRecordUnreadCount = false;
			// 不存聊天室、thread、circle Channel消息
			if (
				dt.chatType === 'chatRoom' ||
				dt.chatThread ||
				dt.isChatThread
			) {
				return;
			}

			// 群聊
			if (dt.chatType === 'groupChat') {
				// from存在并且不是自己发送的消息
				isFromOtherMsg = Boolean(dt.from && dt.from !== this.user);
			} else if (dt.chatType === 'singleChat') {
				// 给指定设备发消息to的格式为`userId/deviceId`
				const idx = dt.to.indexOf('/');
				const messageTo = idx > -1 ? dt.to.substring(0, idx) : dt.to;
				if (
					this.user === messageTo &&
					dt.from &&
					dt.from !== this.user
				) {
					// 单聊 to === user && from && from !== this.user 表示为收到/发送给自己的消息
					isFromOtherMsg = true;
					// 给自己发消息，dt.from为空字符串, 所以取dt.to
					conversationId = dt.from || dt.to;
				}
			}

			if (
				dt.type === 'audio' ||
				dt.type === 'video' ||
				dt.type === 'file' ||
				dt.type === 'combine' ||
				dt.type === 'img'
			) {
				// @ts-ignore
				delete dt.onFileUploadComplete;
				// @ts-ignore
				delete dt.onFileUploadError;
				// @ts-ignore
				delete dt.onFileUploadProgress;
			}

			if (dt.type !== 'combine') {
				delete dt.success;
				delete dt.fail;
			}

			Object.assign(dt, {
				sessionId: getSessionId({
					conversationId,
					conversationType: dt?.chatType,
				}),
				status,
				serverMsgId: dt.id,
			});
			const dbMsg = await this.getMessageByServerMsgId(dt.id);
			if (dbMsg?.status !== MessageStatus.SUCCESS) {
				await this.putMessageToDB(dt);
			}
			if (status > MessageStatus.INPROGRESS) {
				Object.assign(updateInfo, {
					updateTime: dt.time,
					lastMessageId: dt.id,
				});
			}
			// 收到消息需要记录未读数量
			if (isFromOtherMsg) {
				isRecordUnreadCount = true;
				Object.assign(updateInfo, {
					lastMessageFromOtherId: dt.id,
				});
			}

			await this.storeConversation(
				conversationId,
				dt.chatType as ConversationType,
				updateInfo,
				isHistoryMessage ? false : isRecordUnreadCount
			);
		} catch (error) {
			logger.debug('store message error', error);
		}
	}
	/**
	 * 更新发送的本地消息 （发送状态、服务端消息Id）
	 * @param localMsgId 消息localId
	 * @param updateInfo 更新的内容对象
	 */
	async updateLocalMessage(
		localMsgId: string,
		updateInfo: MessageUpdateOptions = {}
	) {
		try {
			const dbMsg = await this.getMessageByServerMsgId(localMsgId);
			if (dbMsg) {
				await this.putMessageToDB({ ...dbMsg, ...updateInfo });
				await this.updateLocalConversation(
					getSessionId({
						conversationId: dbMsg.to,
						conversationType: dbMsg.chatType as ConversationType,
					}),
					{
						lastMessageId: updateInfo.serverMsgId,
						updateTime: dbMsg.time,
					}
				);
			}
		} catch (error) {
			logger.debug('update local message error', error);
		}
	}

	/**
	 * 更新本地会话
	 * @param sessionId sessionId
	 * @param info 会话其他信息
	 */
	async updateLocalConversation(
		sessionId: string,
		info: ConversationUpdateInfo
	) {
		try {
			return this.getConversationBySessionId(sessionId).then(
				(conversation) => {
					if (conversation) {
						const cvs = { ...conversation, ...info };
						this.putConversationToDB(cvs);
						this.conversationMap[sessionId] = cvs;
					}
				}
			);
		} catch (error) {
			logger.debug('update local conversation error', error);
		}
	}

	/**
	 *
	 * @param serverMsgId 消息服务端id
	 * @returns
	 */
	async removeMsgByServerMsgId(serverMsgId: string) {
		try {
			const dt = await this.localCache?.getFromIndex(
				'message',
				'serverMsgId',
				serverMsgId
			);
			return this.localCache?.delete('message', dt?.id || '');
		} catch (error) {
			logger.debug('remove msg by serverId error', error);
		}
	}

	/**
	 * 删除会话本地消息
	 * @param sessionId sessionId
	 */
	async removeMsgBySessionId(sessionId: string) {
		try {
			let cursor = await this.localCache
				?.transaction('message', 'readwrite')
				.store.index('sessionId')
				.openCursor(IDBKeyRange.only(sessionId));
			while (cursor) {
				await cursor.delete();
				cursor = await cursor.continue();
			}
		} catch (error) {
			logger.debug('remove msg by sessionId', error);
		}
	}

	/**
	 * 向本地数据库插入消息
	 * @param message
	 */
	async putMessageToDB(message: LocalMessageType) {
		try {
			return await this.localCache?.put('message', message);
		} catch (error) {
			logger.debug('put msg to local error', error);
		}
	}
	/**
	 * 获取会话最新的消息
	 * @param message
	 */
	async getConversationLastMessage(
		conversationId: string,
		conversationType: ConversationType
	) {
		try {
			const sessionId = getSessionId({
				conversationId,
				conversationType,
			});
			const cursor = await this.localCache
				?.transaction('message')
				.store?.index('sessionTime')
				.openCursor(
					IDBKeyRange.bound(
						[sessionId, START_TIME.getTime()],
						[sessionId, new Date().getTime()]
					),
					'prev'
				);
			if (cursor) {
				return cursor.value;
			} else {
				return undefined;
			}
		} catch (error) {
			logger.debug('get conversation last msg error', error);
		}
	}
	/**
	 * 根据serverMsgId 获取消息对象
	 * @param serverMsgId 服务端消息id
	 * @returns `Promise<LocalDB[Name]["value"] | undefined>`
	 */
	async getMessageByServerMsgId(serverMsgId: string) {
		try {
			return await this.localCache?.getFromIndex(
				'message',
				'serverMsgId',
				serverMsgId
			);
		} catch (error) {
			logger.debug('get msg by serverId error', error);
		}
	}
	/**
	 * 清除指定store的数据
	 * @returns `Promise<void>`
	 */
	async clearStoreData(storeName: LocalDBStoreName) {
		try {
			return await this.localCache?.clear(storeName);
		} catch (error) {
			logger.debug('clear store data error', error);
		}
	}
	/**
	 * 清除缓存会话数据
	 */
	clearConversationMap() {
		this.conversationMap = {};
	}
}
export { getSessionId };
export { LocalCache };
